<template>
  <div class="container mt-5">
    <h2 class="mb-4">회원가입(사업자)</h2>
    <form @submit.prevent="submitForm">
      <div class="mb-3">
        <label for="mname" class="form-label">이름</label>
        <input
          type="text"
          v-model="mname"
          class="form-control"
          id="mname"
          required
        />
      </div>
      <div class="mb-3">
        <label for="memail" class="form-label">이메일</label>
        <div class="input-group">
          <input
            type="email"
            v-model="memail"
            class="form-control"
            id="memail"
            required
            placeholder="더치오더 아이디로 사용됩니다"
            @blur="validateEmail"
          />
          <div class="btn-verify">
            <button
              type="button"
              class="btn btn-primary"
              @click="checkDuplicates"
            >
              중복확인
            </button>
          </div>
        </div>
      </div>

      <div class="mb-3">
        <label for="ecode" class="form-label">인증번호</label>
        <div class="input-group">
          <input
            type="text"
            v-model="ecode"
            class="form-control"
            id="ecode"
            required
            placeholder="이메일로 전송된 인증번호를 입력하세요"
          />
          <button type="button" class="btn btn-primary" @click="submitForm">
            확인
          </button>
        </div>
      </div>
      <div class="mb-3">
        <label for="mpw" class="form-label">비밀번호</label>
        <input
          type="password"
          v-model="mpw"
          class="form-control"
          id="mpw"
          required
        />
      </div>
      <div class="mb-3">
        <label for="confirm_mpw" class="form-label">비밀번호 확인</label>
        <input
          type="password"
          v-model="confirm_mpw"
          class="form-control"
          id="confirm_mpw"
          required
        />
      </div>
      <div class="mb-3">
        <label for="mtel" class="form-label">연락처</label>
        <input
          type="tel"
          v-model="mtel"
          class="form-control"
          id="mtel"
          pattern="[0-9]{3}-[0-9]{4}-[0-9]{4}"
          required
          placeholder="예시: 010-1234-5678"
        />
      </div>
      <div class="center-btn">
        <button type="submit" class="btn btn-primary">가입하기</button>
      </div>
    </form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      mname: "",
      memail: "",
      ecode: "",
      mpw: "",
      confirm_mpw: "",
      mtel: "",
      mgroup_mikey: "100",
      mgroup_makey: "102",
      mstatus_mikey: "200",
      mstatus_makey: "201",
      hiredate: "",
    };
  },
  mounted() {},
  methods: {
    async submitForm() {
      if (
        !this.mname.trim() ||
        !this.memail.trim() ||
        !this.mpw.trim() ||
        !this.mtel.trim()
      ) {
        alert("모든 필드를 채워주세요.");
        return;
      }

      // 비밀번호와 비밀번호 확인이 일치하는지 확인
      if (this.mpw !== this.confirm_mpw) {
        alert("비밀번호가 일치하지 않습니다.");
        return;
      }

      try {
        const response = await axios.post("/business/join", {
          mname: this.mname,
          memail: this.memail,
          mpw: this.mpw,
          mtel: this.mtel,
          mgroup_makey: "100", // 회원 구분 대분류
          mgroup_mikey: "102", // 회원 구분 중분류 101=개인 / 102=사업자 / 103=관리자
          mstatus_makey: "200", //
          mstatus_mikey: "201", // 계정 상태 구분 중분류:201=생존 / 202=탈퇴
        });

        if (response.status === 200) {
          alert("회원가입이 완료되었습니다.");
          this.$router.push("/login/business");
        } else {
          alert("회원가입에 실패했습니다.");
        }
      } catch (error) {
        console.error("회원가입 오류:", error);
        alert("회원가입에 실패했습니다.");
      }
    },

    validateEmail() {
      const re =
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if (!re.test(this.memail)) {
        alert("유효하지 않은 이메일 형식입니다.");
        this.memail = "";
      }
    },
    async checkDuplicates() {
      if (!this.memail.trim()) {
        alert("이메일을 입력해주세요.");
        return;
      }
      try {
        const response = await axios.get(
          `/check-duplicate/memail?memail=${this.memail}`
        );
        if (response.data.isDuplicate) {
          alert("이미 사용 중인 이메일입니다.");
        } else {
          alert("사용 가능한 이메일입니다.");
        }
      } catch (error) {
        console.error("이메일 중복 확인 오류: ", error);
        alert("이메일 중복 확인 중 문제가 발생했습니다.");
      }
    },
  },
};
</script>

